form {
    max-width: 90%;
    .field {
        .control {
            position: relative;
            .input,
            .textarea {
                box-shadow: none;
                border: 1px solid $primaryColor;
            }

            .label {
                color: $primaryColor;
                position: absolute;
                top: -13px;
                left: 20px;
                background-color: $whiteColor;
                padding-left: 10px;
                padding-right: 10px;

                .custom-lable {
                    color: $primaryColor;
                    font-weight: 700;
                    font-size: 1rem;
                }

                span {
                    font-weight: 400;
                    font-size: 13px;
                    color: #726F6F;
                }
            }
        }
        .button {
            background-color: $primaryColor;
            color: $whiteColor;
            font-family: $mainFont;
            font-weight: 700;
            text-transform: uppercase;
        }
    }

    // PARSLEY
    .parsley-errors-list {
        color: #e91e63;
    }
}

form > .field {
    margin-bottom: 25px !important;
}
