// NAVBAR
.navbar {
    padding-left: 25px;
    padding-right: 25px;
    background-color: transparent;
    .navbar-brand {
        img {
            max-height: none;
            height: 75px;
            width: 75px;
        }
    }

    .navbar-menu {

        .navbar-item {
            color: $primaryColor;
            font-size: 1.15rem;
            font-weight: 500;
            padding: .5em 1em;

            .button.lang {
                font-size: 1.15rem;
                color: $primaryColor;
                font-weight: 600;
                font-family: 'cairo';
                border-color: $primaryColor;
                border-width: 2px;
            }
        }
    }
}