html {
    font-size: $mainFontSize;

    body {
        font-family: $mainFont;
        line-height: $lineHeight;
        font-weight: 300;
        color: $blackColor;
        background-color: $whiteColor;

        .navbar-dots {
            position: fixed;
            z-index: 4;
            top: calc(50% - 3.25rem);
            left: 50px;
            transform: translateX(-50%);

            a {
                color: $primaryColor;
            }
        }

        
        .section-container {
            padding: 1.5rem;
        }

        section {
            min-height: calc(100vh - 6.25rem);
            position: relative;
            display: none;
            overflow: hidden;
            padding-left: 80px;

            .section-title {
                
                position: absolute;
                bottom: 25px;
                right: 1.5rem;
                z-index: 3;
            }
        }

        section.active {
            display: block;
        }
    }
}